
.banner{
    width: 100%;
    height: $bannerImgHeight;
    position: relative;
    img{
        width: 100%;
        height: $bannerImgHeight;
        object-fit: cover;
        border-radius: $borderRadius;
        object-position: center 44%;
        filter: opacity(90%) brightness(70%);
        @media screen and (max-width: 600px) {
            height: $bannerImgHeight*3/4; 
        }
    }
    h1{
        width: 100%;
        padding: 0;
        margin: 0;
        color: white;
        font-size: $base-font-size*3;
        font-weight: 500;
        text-align: center;
        position: absolute;
        top: 2rem;
        @media screen and (max-width: 780px) {
            top: 1rem;
        }
        @media screen and (max-width: 600px) {
            font-size: $base-font-size*2;
        }
    }
}

