.navigation{
    width: 30%;
    margin-left: 0;
    transition: ease all .5s;
    @media screen and (max-width: 800px) {
        width: 50%;
    }
    ul{ 
        list-style-type: none;
        display: flex;
        justify-content: space-between;
    } 
    // li{
    //     list-style-type: none;
    // }
    a{
        text-decoration: none;
        color: $primary;
    }
    &__nav.active{
        border-bottom: thin solid $primary;
    }
}

